import React from "react";
import { useEffect, useState } from "react";

import '../../../CSS/project_management.css'

import * as Request from "../../../service/restDataApi";

function DataMain()
{
    const [server_ip, setServer_ip] = useState();

    /*      */
    useEffect( () =>
    {
        //  ip  
        Request.requestGetServerIp().then(result =>
        {
            var host = window.location.hostname
            setServer_ip('http://' + host + ':' + result['port'])
        })
        .catch(error =>
        {
            alert('get ip info error')
        });

//        var host = window.location.hostname
//        setServer_ip('http://' + host + ':9090')

    }, []);


    return (
        <>
        {/*     */}
        <div className='manage_container'>

            {/*     -  */}
            <div className='manage_header' style={{width:'100%'}}>
                {/*   */}
                <div className='path'></div>

                <div className='title'>
                    <div className='title_left'>Data Management</div>
                </div>
            </div>

            <div className='data_manage_content' style={{backgroundColor:'#242933'}}>
                 <iframe id='labelTool'  className='project_manage_content' title='labeling tool' src={server_ip} frameBorder='0' style={{ width:'100%', height:'100%'}}></iframe>
            </div>

        </div>
        </>
    );
}

export default DataMain;